    font-size: 62.5%;
  }
}

*, *:before, *:after {
  box-sizing: inherit;
}

body {
  font-family: "Open Sans", sans-serif;
  font-size: 1.6rem;
  background-color: #a5a2a2;
  height: 100vh;
}
body.fijar-body {
  overflow: hidden;
}

.contenedor {
  width: 95%;
  max-width: 1200px;
  margin: 0 auto;
}

img {
  max-width: 100%;
  height: auto;
  display: block;
}

ul {
  padding: 0;
  list-style: none;
  margin: 0;
}

main {
  background-image: linear-gradient(to top, #352ed3, #382ed0, #3b2ecc, #3d2ec9, #3f2ec6, #412ec7, #422ec9, #442eca, #462ed0, #482ed5, #4b2edb, #4d2ee1);
  background-repeat: no-repeat;
}

h1, h2, h3 {
  margin: 0;
  line-height: 1.2;
}

h1 {
  font-size: 4.4rem;
  font-family: "Open Sans", sans-serif;
}

h2 {
  font-size: 4rem;
  font-family: "Roboto", cursive;
}

h3 {
  font-size: 3rem;
}

[class$=heading] {
  margin: 2rem 0;
}

a {
  text-decoration: none;
  color: #000;
}

p {
  color: #FFFFFF;
  font-size: 1.6rem;
  line-height: 1.8;
  font-family: "Open Sans", sans-serif;
}

.afiliates__conatiner {
  padding-top: 5rem;
}
.afiliates__conatiner .afiliates__user {
  width: auto;
  padding: 2rem;
  background-color: #01081f;
  border-radius: 0.5rem;
  margin: 0 auto;
  text-align: center;
  display: flex;
  align-items: center;
  justify-content: space-around;
  box-shadow: -3px 8px 5px 0px rgba(0, 0, 0, 0.41), -2px 3px 5px rgba(71, 70, 70, 0.267);
}
@media (min-width: 768px) {
  .afiliates__conatiner .afiliates__user {
    max-width: 400px;
  }
}
.afiliates__conatiner .afiliates__user .nmr {
  font-size: 5rem;
  font-weight: bold;
  color: #FFFFFF;
}
.afiliates__conatiner .afiliates__user .icon {
  font-size: 4rem;
  color: #FFFFFF;
}
.afiliates__conatiner .afiliates__flex {
  display: flex;
  justify-content: center;
  justify-content: space-between;
  flex-direction: column;
  gap: 3rem;
  padding-top: 4rem;
  align-items: flex-start;
}
@media (min-width: 768px) {
  .afiliates__conatiner .afiliates__flex {
    flex-direction: row;
  }
}
.afiliates__conatiner .afiliate__card {
  box-shadow: -3px 8px 5px 0px rgba(0, 0, 0, 0.41), -2px 3px 5px rgba(71, 70, 70, 0.267);
  background-color: #01081f;
  border-radius: 0.5rem;
  width: 100%;
}
@media (min-width: 768px) {
  .afiliates__conatiner .afiliate__card {
    width: 50%;
  }
}
.afiliates__conatiner .afiliate__card .card__header {
  border-bottom: 1px solid rgba(204, 204, 204, 0.322);
  padding: 2rem;
}
.afiliates__conatiner .afiliate__card .card__header h2 {
  font-size: 2rem;
  color: #FFFFFF;
}
.afiliates__conatiner .afiliate__card .card__body {
  padding: 2rem;
}
.afiliates__conatiner .afiliate__card .card__body ul .rewar-item {
  padding: 1rem 2rem;
  color: #FFFFFF;
  display: inline-block;
  margin: 0.2rem 0;
  transition: all 0.5s ease;
  width: 100%;
  border-radius: 0.8rem;
  font-size: 2rem;
}
.afiliates__conatiner .afiliate__card .card__body ul .rewar-item:hover {
  color: #01081f;
  background-color: #FFFFFF;
}
.afiliates__conatiner .afiliate__card .card__body input[type=text] {
  appearance: none;
  width: 100%;
  outline: none;
  border: none;
  border-radius: 0.5rem;
  height: 3rem;
  padding-inline: 2rem;
  background-color: #FFFFFF;
}
.afiliates__conatiner .afiliate__card .card__table {
  overflow: hidden;
  overflow-x: auto;
}
.afiliates__conatiner .afiliate__card .card__table .tabla {
  border-collapse: collapse;
  width: 100%;
}
.afiliates__conatiner .afiliate__card .card__table .tabla tr {
  border-bottom: 2px solid #412ec2;
}
.afiliates__conatiner .afiliate__card .card__table .tabla th {
  font-weight: 500;
  font-size: 1.8rem;
}
.afiliates__conatiner .afiliate__card .card__table .tabla td,
.afiliates__conatiner .afiliate__card .card__table .tabla th {
  padding: 1rem 2rem;
  color: #FFFFFF;
  font-weight: 300;
}
.afiliates__conatiner .afiliate__card .card__table .tabla td {
  color: #adaaaa;
}
.afiliates__conatiner .afiliate__card .card__table .tabla tr:hover {
  background-color: rgba(221, 221, 221, 0.116);
}
.afiliates__conatiner .afiliate__card .card__table .tabla th {
  padding-top: 12px;
  padding-bottom: 12px;
  text-align: left;
}
.afiliates__conatiner .titulo__banner {
  color: #FFFFFF;
  margin: 3rem 0;
}
.afiliates__conatiner .banner {
  margin-top: 5rem;
  width: 100%;
  padding: 2rem;
  display: flex;
  flex-direction: column;
  align-items: center;
  gap: 2rem;
}
@media (min-width: 768px) {
  .afiliates__conatiner .banner {
    flex-direction: row;
    justify-content: space-around;
  }
}
.afiliates__conatiner .banner.mt-0 {
  margin-top: 0;
}
.afiliates__conatiner .banner.reverse {
  flex-direction: column;
}
@media (min-width: 768px) {
  .afiliates__conatiner .banner.reverse {
    flex-direction: row-reverse !important;
  }
}
.afiliates__conatiner .banner.column {
  flex-direction: column;
}
.afiliates__conatiner .banner .banner__img {
  margin: 0 auto;
  width: auto;
}
@media (min-width: 768px) {
  .afiliates__conatiner .banner .banner__img {
    width: 300px;
  }
}
.afiliates__conatiner .banner .banner__img.large1 {
  width: auto;
}
@media (min-width: 1024px) {
  .afiliates__conatiner .banner .banner__img.large1 {
    width: 800px;
  }
}
.afiliates__conatiner .banner .banner__inputs {
  width: 100%;
}
@media (min-width: 768px) {
  .afiliates__conatiner .banner .banner__inputs {
    width: 50%;
  }
}
.afiliates__conatiner .banner .banner__inputs textarea {
  overflow: hidden;
  background-color: #412EC2;
  color: #FFFFFF;
  padding: 2rem;
  width: 100%;
  max-width: 100%;
  height: 120px;
  max-height: 200px;
  border: none;
  outline: none;
  border-radius: 0.5rem;
}

.suppor__container .support_title {
  margin-top: 3rem;
  font-weight: bold;
  color: #FFFFFF;
}
.suppor__container .support__flex {
  display: flex;
  transition: all 0.5s ease;
  flex-direction: column;
  gap: 2rem;
  margin-top: 3rem;
}
@media (min-width: 768px) {
  .suppor__container .support__flex {
    flex-direction: row;
  }
}
.suppor__container .support__flex.-m {
  align-items: flex-start;
}
.suppor__container .support__col {
  background-color: #01081f;
  flex-basis: 50%;
  border-radius: 0.5rem;
  padding: 2rem;
  box-shadow: -3px 8px 5px 0px rgba(0, 0, 0, 0.41), -2px 3px 5px rgba(71, 70, 70, 0.267);
  display: flex;
  align-items: center;
  justify-content: space-between;
}
.suppor__container .support__col .support__icon i {
  width: 5rem;
  height: 5rem;
  border-radius: 50%;
  background-color: rgba(255, 255, 255, 0.315);
  text-align: center;
  line-height: 5rem;
  font-size: 2.8rem;
  color: #00C9B7;
}
.suppor__container .support__col .description {
  display: flex;
  flex-direction: column;
  align-items: center;
}
.suppor__container .support__col .description span {
  color: #FFFFFF;
  font-size: 2.2rem;
  font-weight: 600;
}
.suppor__container .support__col .description strong {
  font-size: 2.3rem;
  color: #FFFFFF;
}

.support__form {
  background-color: #01081f;
  border-radius: 0.5rem;
  padding: 2rem;
  box-shadow: -3px 8px 5px 0px rgba(0, 0, 0, 0.41), -2px 3px 5px rgba(71, 70, 70, 0.267);
  width: 100%;
}
@media (min-width: 768px) {
  .support__form {
    flex-basis: 50%;
  }
}
.support__form .col__form {
  display: flex;
  gap: 1rem;
  flex-direction: column;
}
@media (min-width: 480px) {
  .support__form .col__form {
    flex-direction: row;
  }
}
.support__form .campo {
  display: flex;
  flex-direction: column;
  gap: 1rem;
  width: 100%;
}
.support__form .campo .campo__label {
  font-weight: 500;
  color: #FFFFFF;
}
.support__form .campo .campo__input {
  width: 100%;
}
.support__form .campo .campo-m {
  width: 50%;
}
.support__form .campo.mt-1 {
  margin-top: 1rem;
}
.support__form .campo textarea {
  max-width: 100%;
}
@media (min-width: 768px) {
  .support__form .campo textarea {
    max-width: 500px;
    max-height: 200px;
  }
}

.support__tablet {
  width: 100%;
}
@media (min-width: 768px) {
  .support__tablet {
    width: 50%;
  }
}
.support__tablet .table__container .table-box {
  border-radius: 0.5rem;
  overflow-y: auto;
  background: #01081f;
  box-shadow: -3px 8px 5px 0px rgba(0, 0, 0, 0.41), -2px 3px 5px rgba(71, 70, 70, 0.267);
  scrollbar-gutter: stable;
}
.support__tablet .table__container .table-box::-webkit-scrollbar {
  width: 10px;
}
.support__tablet .table__container .table-box::-webkit-scrollbar-track {
  background-color: #412EC2;
  border-top-left-radius: 0;
  border-top-right-radius: 19px;
  border-bottom-right-radius: 20px;
  overflow: hiden;
}
.support__tablet .table__container .table-box::-webkit-scrollbar-thumb {
  box-shadow: inset 0 0 6px rgba(0, 0, 0, 0.3);
  border-top-right-radius: 19px;
  border-bottom-right-radius: 20px;
}
.support__tablet .table__container .table-box table {
  width: 100%;
}
.support__tablet .table__container .table-box table thead {
  transition: all 0.5s linear;
}
.support__tablet .table__container .table-box table th {
  padding-top: 12px;
  padding-bottom: 12px;
  color: #FFFFFF;
}
.support__tablet .table__container .table-box table tr {
  border-bottom: 2px solid #412EC2;
}
.support__tablet .table__container .table-box table,
.support__tablet .table__container .table-box th,
.support__tablet .table__container .table-box td {
  border-collapse: collapse;
  text-align: left;
  color: #FFFFFF;
}